@media (min-width: 600px) and (max-width: 1200px){
  .container {
    width: 100%;
  }
  .profileInfo {
    width: 85%;
    margin: 0 auto;
  }
  button {
    display: flex;
    margin-top: 10px;
    margin-left: 40px;
  }
  h1, h2, h3 {
    margin: 0;
  }
  .mytech-wrapper,
  .skills-wrapper,
  .studies-wrapper{
    margin: 0 60px;
  }
  .case-title {
    min-height: 50px;
  }
  .MyCases {
  width: calc(100% - 60px);
  margin: 0 30px;
  justify-content: center;
  }
}

@media (max-width: 600px) {
  body {
    width: 1000px;
  }
  .profileInfo {
    width: 85%;
    margin: 0 auto;
  }
  h1 {
    margin: 0;
  }
  h2 {
    margin-top: 100px;
  }
  button {
  margin-top: 0;
  margin-left: 40px;
  }
  .MyCases {
  width: calc(100% - 120px);
  margin: 0 60px;
  flex-flow: column wrap;
  }
  .Cases {
    width: calc(100% - 20px);
  }
  .Cases p {
    font-size: 30px;
  }
  .caseImage {
    width: 100%;
    height: auto;
  }

  .mytech-wrapper,
  .skills-wrapper,
  .studies-wrapper{
    margin: 0 60px;
  }
  .project-container {
    width: 90%;
  }
  .skills-list-projects-container{
    margin-bottom: 100px;
  }
  footer {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
  }

}
