@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css?family=Fredoka+One|Source+Sans+Pro);
* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  font-family: sans-serif; }

li {
  list-style: none;
  margin: 10px;
  color: #737373;
  font-size: 14px; }

ul {
  margin: 0;
  padding: 0; }

h1, h2 {
  font-family: 'Fredoka One';
  text-transform: uppercase;
  color: #82a79f;
  letter-spacing: 1px;
  font-size: 40px; }

h3 {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 25px;
  color: #737373;
  margin-right: 50px;
  letter-spacing: 1px; }

h5 {
  font-family: 'Fredoka One';
  text-transform: uppercase;
  font-size: 22px;
  color: #d1e7d2;
  letter-spacing: 1px; }

h6 {
  margin: 10px;
  font-size: 12px;
  font-family: 'Fredoka One';
  text-transform: uppercase; }

button {
  display: flex;
  justify-content: center;
  align-self: center;
  font-family: 'Fredoka One';
  text-transform: uppercase;
  background-color: #d1e7d2;
  outline: none;
  border-style: none;
  width: 160px;
  border-radius: 40px;
  padding: 20px 20px;
  cursor: pointer;
  letter-spacing: 1px;
  margin-top: 30px;
  transition: 0.2s;
  opacity: 1; }

section {
  color: #82a79f;
  font-family: 'Fredoka One';
  cursor: pointer;
  font-size: 18px; }

button:hover {
  opacity: 0.8; }

a {
  color: #82a79f;
  text-decoration: none; }

.container {
  width: 1000px;
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  margin: 0 auto; }

.profileInfo {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center; }

.profileInfo img {
  width: 250px;
  height: 300px;
  border-radius: 10px; }

.aboutMeText {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 40px; }

.aboutMeText p {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 15px;
  color: #737373;
  margin-right: 80px;
  letter-spacing: 1px;
  line-height: 25px; }

.mytech-wrapper {
  display: flex;
  flex-flow: column;
  margin: 0 30px; }

.myTech p {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 20px;
  color: #737373;
  margin-right: 50px; }

.MyCases {
  display: flex;
  flex-flow: row wrap;
  overflow: auto;
  width: 90%;
  margin-top: 30px;
  margin: 30px 30px 0 30px; }

.Cases {
  height: auto;
  width: calc(50% - 20px);
  margin: 10px 0;
  padding: 10px; }

.Cases p {
  margin: 0;
  color: #82a79f;
  font-size: 18px; }

.Cases a {
  text-decoration: none;
  width: 80%;
  font-family: 'Source Sans Pro', sans-serif; }

.caseImage {
  margin-top: 20px;
  width: 100%;
  height: 255px;
  overflow: hidden; }

.caseImage img {
  width: 100%;
  height: 100%; }

.case-title {
  min-height: 20px; }

.myCaseText {
  display: flex;
  flex-wrap: wrap; }

.skills-wrapper {
  display: flex;
  flex-flow: column;
  margin: 0 30px; }

.skillsList {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px; }

.studies-wrapper {
  display: flex;
  flex-flow: column;
  margin: 0 30px; }

.studiesList {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  font-size: 15px;
  line-height: 30px; }

.studiesListItemFirst, .studiesListItem {
  width: 50%;
  margin-right: 30px; }

.studiesList p {
  color: #737373;
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0; }

.project-container {
  width: 1000px;
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  margin: auto; }

.heroImage {
  margin: 30px 0;
  width: 100%;
  height: 500px;
  overflow: hidden;
  object-fit: cover; }

.heroImage img {
  width: 100%;
  height: 100%; }

.project-text-container {
  width: 70%;
  height: auto;
  margin-bottom: 30px; }

.project-text-container P {
  width: 60%;
  list-style: none;
  margin: 10px;
  color: #737373;
  font-size: 16px;
  line-height: 30px; }

.skills-list-projects-container {
  display: flex;
  width: 50%; }

.skills-list-projects {
  display: flex;
  flex-flow: column;
  width: 100%; }

footer {
  width: 100%;
  font-family: 'Source Sans Pro', sans-serif;
  border-top: 1px solid #000;
  height: 150px;
  margin-top: 40px;
  background-color: #82a79f;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 1px; }

@media (min-width: 600px) and (max-width: 1200px) {
  .container {
    width: 100%; }
  .profileInfo {
    width: 85%;
    margin: 0 auto; }
  button {
    display: flex;
    margin-top: 10px;
    margin-left: 40px; }
  h1, h2, h3 {
    margin: 0; }
  .mytech-wrapper,
  .skills-wrapper,
  .studies-wrapper {
    margin: 0 60px; }
  .case-title {
    min-height: 50px; }
  .MyCases {
    width: calc(100% - 60px);
    margin: 0 30px;
    justify-content: center; } }

@media (max-width: 600px) {
  body {
    width: 1000px; }
  .profileInfo {
    width: 85%;
    margin: 0 auto; }
  h1 {
    margin: 0; }
  h2 {
    margin-top: 100px; }
  button {
    margin-top: 0;
    margin-left: 40px; }
  .MyCases {
    width: calc(100% - 120px);
    margin: 0 60px;
    flex-flow: column wrap; }
  .Cases {
    width: calc(100% - 20px); }
  .Cases p {
    font-size: 30px; }
  .caseImage {
    width: 100%;
    height: auto; }
  .mytech-wrapper,
  .skills-wrapper,
  .studies-wrapper {
    margin: 0 60px; }
  .project-container {
    width: 90%; }
  .skills-list-projects-container {
    margin-bottom: 100px; }
  footer {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0; } }

